/**
* 添加和编辑
* @module 案件
* @desc 票据管理添加删除
* @author 乔亚军
* @date 2019-6-4
**/
<template>
    <div>
        <el-dialog
                :close-on-click-modal="false"
                :title="title"
                :visible="show"
                @close="closeDialog"
                center
                width="550px"
        >
            <el-form
                    :model="form"
                    :rules="rules"
                    label-width="130px"
                    ref="ruleForm"
                    size="medium"
                    v-loading="loading"
            >
                <el-row>
                    <el-col :span="24">
                        <el-form-item label="选择组织部门：" >
                            <el-select v-model="form.collarOrg" placeholder="请选择部门"  class="divwidth"  @change="stlectOrg">
                                    <el-option
                                        v-for="item in collarOrgList"
                                        :key="item.orgId"
                                        :label="item.orgName"
                                        :value="item.orgId"
                                        >
                                    </el-option>
                            </el-select>     
                        </el-form-item>
                    </el-col>
                    <el-col :span="24">
                        <el-form-item label="银行名称：" prop="turnBank">
                            <el-select v-model="form.turnBank" placeholder="请选择缴款银行"  class="divwidth">
                                <el-option
                                v-for="item in banklist"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value">
                                </el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    
                    <el-col :span="24">
                        <el-form-item label="银行收据编号：" prop="turnReceiptNum">
                           <el-input  placeholder="填写银行收据编号" v-model="form.turnReceiptNum"></el-input>
                        </el-form-item>
                    </el-col>
                   
                    <el-col :span="24">
                        <el-form-item label="缴纳日期：" prop="turnDay">
                           <el-date-picker
                                    v-model="form.turnDay"
                                    type="date"
                                    placeholder="选择日期" 
                                    class="divwidth">
                            </el-date-picker>
                        </el-form-item>
                    </el-col>
                    <el-col :span="24">
                        <el-form-item label="票据清单：" prop="detailId">
                            <el-select v-model="form.detailId" multiple  placeholder="请选择票据清单"  class="divwidth">
                                <el-option-group
                                v-for="group in billlist"
                                :key="group.label"
                                :label="group.label">
                                <el-option
                                    v-for="item in group.options"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.value">
                                </el-option>
                                </el-option-group>
                            </el-select>
                        </el-form-item>
                    </el-col>
                </el-row>
            </el-form>

            <span class="dialog-footer" slot="footer">
        <el-button @click="closeDialog" size="small" type="info">
          <i class="iconfont icon-back"></i>取 消
        </el-button>
        <el-button @click="submitForm('ruleForm')" size="small" type="primary">
          <i class="iconfont icon-save"></i>确 定
        </el-button>
      </span>
        </el-dialog>
    </div>
</template>apiOrgSearch
<script>
    import {apiBankReceiptListListGetBill,apibankReceiptListAdd} from "../../../../../api/case";
    import {apiOrgSearch} from "../../../../../api/system";

    export default {
        name: "edit",
        props: {
            caseProcessList: {
                type: Array,
                default: []
            },
            show: {
                type: Boolean,
                default: false
            },
            title: {
                type: String,
                default: "编辑"
            },
            type: {
                type: String,
                default: ""
            }
        },

        data() {
            return {
                billlist:[],
                banklist:[{value:"建设银行",label:"建设银行"},{value:"工商银行",label:"工商银行"}],
                state1: "",
                restaurants: [],
                collarOrgList:[],
                loading: false,
                quotaInvoiceNumArr: [
                    {value: "10", label: "10"},
                    {value: "20", label: "20"},
                    {value: "50", label: "50"},
                    {value: "100", label: "100"}
                ],
                //发送数据
                form: {},
                form2: {
                    pageSize: 20,
                    pageNum: 1,
                },
                //表单验证规则
                rules: {
            
                    turnReceiptNum: [
                        {required: true, message: "请输入银行收据编号", trigger: "blur"},
                    ],
                    detailId: [
                        {required: true, message: "请选择票据清单", trigger: "change"},
                    ],
                    turnBank: [
                        {required: true, message: "请选择银行名称", trigger: "change"}
                    ],
                    turnDay: [
                        {required: true, message: "选择日期", trigger: "change"}
                    ],
                }
            };
        },
        methods: {
            //关闭弹框
            closeDialog() {
                this.$emit("update:show", false);
            },
            //选择部门
            stlectOrg(val){
                apiBankReceiptListListGetBill({collarOrg:val}).then(res => {
                   let arr = [];
                   let obj = {};
                   res.data.forEach(ele => {
                      let list = ele.caseQuotaDetailList;
                      obj ={
                          label:ele.quotaInvoiceNum,
                          options:[]
                      } 
                      let innerobj = {};
                      list.forEach(item=>{
                         innerobj = {
                             value:item.detailId,
                             label:item.quotaInvoiceNum
                         }
                        obj.options.push(innerobj);
                      });
                      arr.push(obj);
                   });
                  
                   this.billlist = arr
                });
            },
            //提交
            submitForm() {
                this.$refs.ruleForm.validate(valid => {
                    if (valid) {
                        this.loading=true;
                        this.form.turnDay = Date.parse(this.form.turnDay);
                        this.form.detailId = this.form.detailId.join(",");
                        //添加
                        apibankReceiptListAdd(this.form).then(res => {
                            this.$emit('getList');
                            this.$message({
                                type: "success",
                                message: "添加成功!"
                            });
                            this.loading = false;
                            this.closeDialog();
                        });
                        
                    }
                });
            },

            //弹框弹出时获取data给form
            initPage() {
                //部门
                apiOrgSearch(this.form).then(res => {
                    this.collarOrgList = res.data;
                });
                 this.form = {};
            }
        },

        //监听，如果show为true,则执行initPage()
        watch: {
            show(value) {
                if (value) {
                    this.initPage();
                    this.$nextTick(() => {
                        this.$refs.ruleForm.clearValidate();
                    });
                }
            }
        }
    };
</script>
<style lang='less' scoped>
    .ipt-css {
        width: 150px
    }

    .alert_wrop {
        padding: 10px;
        display: flex;
        justify-content: space-between;
    }

    .bill {
        display: flex;
    }
    .divwidth{
       width: 100%
    }
</style>


