/**
* 案件->票据管理
* @module 案件
* @desc 票据管理表格
* @author 乔亚军
* @date 2019-6-4
**/
<template>
    <div>
        <div class="search-wrop">
            <lj-search-from :onAdd="buttonCheck('add')?onAdd:''"  :onDel="buttonCheck('batchdel')?onDel:''" 
                             @searchList="searchList"  />
        </div>
        <!-- 表格 -->
        <el-table :data="list" :header-cell-style="{background:'#e9eff8'}" :row-style="{cursor:'pointer'}" v-loading='loading'
                  @selection-change="handleSelectionChange" @row-click="seeFunction"
                  border size="mini">
            <el-table-column type="selection" width="55" />
            <el-table-column align="center" label="收据编号" prop="turnReceiptNum" show-overflow-tooltip />
            <el-table-column align="center" label="缴款金额" prop="totalAmount" show-overflow-tooltip />
            
        </el-table>
        <lj-pagination :pageNum.sync="form.pageNum" :pageSize.sync="form.pageSize" :total="total" @getData="getList"
                       class="page-css"
        />

        <!-- 编辑&添加弹框 -->
        <change-Edit :caseProcessList="caseProcessList" :show.sync="editObj.show" :title="editObj.title"
                     :type="editObj.editId" @getList="getList"/>
      
       
    </div>
</template>

<script>
    import LjPagination from '../../../../../components/LjPagination'
    import LjSearchFrom from '../../../../../components/LjSearchForm'
    import changeEdit from "./editadd";
    import {
        apiCaseQuotaInvoiceDelete,
        apibankReceiptListSearch,
        apiBankReceiptListBatchDelete
    } from "../../../../../api/case";
    import {formatDate,quotaStatus} from "../../../../../until/wordBook";

    export default {
        name: "bankbill",
        data() {
            return {
                caseProcessList: [],
                docStyleArr: [],
                loading: true,
               
                //弹出框数据
                editObj: {
                    show: false,
                    title: '',
                    editId: null
                },
                seeObj: {
                    show: false,
                    title: '',
                    editId: null
                },
                caseTypeList: [],
                form: {
                    pageSize: 20,
                    pageNum: 1,
                    keyWord:""
                },
                total: 0,
                list: [],
                idstr: '',
            }
        },
        components: {
             LjPagination, LjSearchFrom,changeEdit,
        },


        methods: {
           
            //批量选择
            handleSelectionChange(val) {
                let idarr = [];
                val.forEach(item => {
                    idarr.push(item.turnReceiptNum);
                });
                this.idstr = idarr.join(',')
            },

            //查询+搜索
            getList() {
                apibankReceiptListSearch(this.form).then(res => {
                    this.total = res.total;
                    this.list = res.data;
                    this.loading = false;
                });

            },

            //查看
            seeFunction(row) {
                this.seeObj.editId = row.turnReceiptNum;
                this.seeObj.title = '查看票据列表';
                this.seeObj.show = true;
            },

            //新增
            onAdd() {
                this.editObj.editId = null;
                this.editObj.title = '新增银行收据';
                this.editObj.show = true;
            },

            //删除按钮事件
            delateFunction(index, data) {
                this.$confirm('此操作将永久删除该数据, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    apiCaseQuotaInvoiceDelete(data.quotaId).then(() => {
                        this.getList();
                        this.$message({
                            type: 'success',
                            message: '删除成功!'
                        });
                    })
                })
            },

            //批量删除
            onDel() {
                this.$confirm('此操作将永久删除这些数据, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    apiBankReceiptListBatchDelete({turnReceiptNums: this.idstr}).then(() => {
                        this.getList();
                        this.$message({
                            type: 'success',
                            message: '删除成功!'
                        });
                    })
                })
            },
             
            //搜索点击
            searchList(data) {  //data就是接收的this.form数据
                this.loading = true;
                data.pageSize = this.form.pageSize;
                this.form = JSON.parse(JSON.stringify(data));
                this.form.pageNum = 1;
                apibankReceiptListSearch(this.form).then(res => {
                    this.list = res.data;
                    this.total = res.total;
                    this.loading = false;
                })
            },

           
        },
        


        filters: {
            quotaStatus(val) {
                return quotaStatus[val]
            },
            formatDate(value) {
                return formatDate(value, 'yyyy-MM-dd')
            },

        },
        created() {
            this.getList();

        },
    }
</script>

<style scoped>
    .search-wrop {
        margin-bottom: 10px
    }

    .btn-css {
        margin: 0 10px
    }

    .page-css {
        margin-top: 10px
    }
</style>
